.sign-auth-form__submit-wrapper {
  position: absolute;
  top: 50%;
  right: 0;
  width: 70px;
  height: 70px;
  transform: translate(50%, -50%);
  background: var(--primary-black);
  border-radius: 100%;
  z-index: 1;
}
.sign-auth-form__submit-wrapper::before {
  content: "";
  position: absolute;
  top: calc(50% - 1.5px);
  border-radius: 5px;
  right: 60px;
  width: 295px;
  height: 3px;
  z-index: 2;
  background: var(--primary-black);
}

.sign-auth-form__field .sign-auth-form__input {
  color: #FFF;
}

.sign-auth-form__submit {
  display: none;
}

.sign-auth-form__submit-trigger {
  position: relative;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  transform: translate(-50%, -50%);
  background: var(--black);
  border-radius: 100%;
  cursor: pointer;
  z-index: 2;
}

.sign-auth-form__submit_triangle {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.sign-auth-form__submit_triangle::after {
  content: "";
  top: 50%;
  left: calc(50% - 2px);
  position: absolute;
  border: 10px solid transparent;
  border-top: 10px solid var(--primary-black);
  transform: rotate(-90deg) translate(50%, 0);
  transition: 300ms;
}

.valid-auth-form {
  color: #16981a;
}
.valid-auth-form::before {
  border-top-color: #16981a !important;
  border-right-color: #16981a !important;
  border-bottom-color: #16981a !important;
}
.valid-auth-form::after {
  border-top: 2px solid #16981a !important;
  border-left-width: 2px !important;
  border-right-width: 2px !important;
  transform: rotate(270deg);
}
.valid-auth-form .sign-auth-form__submit_triangle::after {
  border-top: 10px solid #16981a;
}

.sign-auth-form__submit-trigger {
  border-radius: 100%;
  box-shadow: none;
}
.sign-auth-form__submit-trigger::before, .sign-auth-form__submit-trigger::after {
  box-sizing: inherit;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 100%;
}
.sign-auth-form__submit-trigger::before {
  border: 2px solid transparent;
  transition: border-top-color 0.15s linear, border-right-color 0.15s linear 0.1s, border-bottom-color 0.15s linear 0.2s;
}
.sign-auth-form__submit-trigger::after {
  border: 0 solid transparent;
  transition: transform 0.4s linear 0s, border-left-width 0s linear 0.35s;
}

