* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* overflow-x: hidden; */
}
*:focus {
  border: none;
  outline: none;
}

.show {
  display: initial !important;
}

.overflow-hidden {
  overflow: hidden;
}

.active-admin-page {
  position: relative;
  background: #212F32;
}
.active-admin-page:hover {
  background: #212F32 !important;
}
.active-admin-page:hover .mat-icon, .active-admin-page:hover span {
  color: #FFF !important;
}
.active-admin-page::before {
  content: "";
  position: absolute;
  width: 3px;
  height: 100%;
  left: 0;
  background-color: #17B4BD;
}

.hor-centering-elems {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 160px;
}
.hor-centering-elems span {
  margin-left: 15px;
  color: #FFF;
}